#sidebar {
    #header {
        background-color: lighten(black, 30%);
        transition: 2s;
        // display: flex;
        // justify-content: space-between;
        #title {
            position: absolute;
            margin-left: 100px;
        }
        svg {
            margin-top: 6px;
            margin-right: 8px;
            cursor: pointer;
        }
    }
    margin:0px;
    // top:0px;
    // position: absolute;
    background-color: black;
    color: white;
    font-size: 24px;
    text-align: right;
    width: 40px;
    height: 100%;
    transition: .5s;
    
    
    
    &.expanded {

        width: 400px;
        
    }
}